.header {
  position: relative;
}
.header-container {
  display: flex;
  justify-content: space-evenly;
  height: 100vh;
  position: relative;
  padding: 0 40px;
  gap: 15px;
  align-items: center;
}

.fondosvg {
  position: absolute;
  height: 100%;
  right: 0;
  width: 100%;
  object-fit: cover;
  /* color-scheme: light dark; */
}

.color-bg-container {
  background: var(--color-bg);
}
.header-content {
  max-width: 358px;
}

.header-title,
.header-title2 {
  font-family: var(--font-family);
  /* color: var(--color-text); */
  font-weight: 600;
  font-size: 4.5rem;
  margin: 0 0 1.5rem 0;
  background: var(--gradients-text);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  line-height: 0.95;
  text-shadow: -3px 2px 1px var(--title-shadow-color);
}
.header-content .titulo2 {
  margin: 0 0 1rem 0;
}

.header-content p {
  margin: 0 0 0 0;
  font-size: 1.1rem;
  /* font-weight: 600; */
  font-family: var(--font-family-1);
  color: var(--color-text);
}

.header-content_input {
  margin: 2rem 0 0 0px;
}

.header-content_input button {
  overflow: hidden;
  font-size: 18px;
  outline: none;
  border: 0;
  background: var(--color-button);
  color: #e9e9e9;
  padding: 0.7rem 2rem 0.5rem 2rem;
  border-radius: 50px;
  font-family: var(--font-family);
  cursor: pointer;
  position: relative;
  z-index: 0;
  border: solid 3px #7267cb00;
  box-shadow: -3px -3px 4px 0px #ffffff1c, 4px 4px 4px 2px #00000026;
}

.header-content_input button::after,
.header-slashButton {
  content: "";
  background: #f9d4d1;
  width: 50px;
  height: 180px;
  position: absolute;
  /* opacity: 0.5; */
  transform: rotate(35deg);
  top: -82px;
  left: 100%;
  transition: 0.4s ease-out;
}
.header-slashButton {
  left: -70px;
  background: #ffffff8c;
}

.header-content_input button:hover .header-slashButton {
  left: 100%;
}

.header-content_input button:active {
  transform: translateY(1px);
  transition: 0.1s;
}

.header-content_input button span {
  font-family: sans-serif;
}

.header-img {
  /* margin: auto 0; */
}

.header-img img {
  max-width: 405px;
}

/* REDESS */
.header-redes {
  margin-top: 30px;
  font-size: 2rem;
  display: grid;
  gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(10px, 35px));
  color: var(--color-icon);
}
.header-redesContent {
  transition: 0.3s ease-out;
  cursor: pointer;
}
.header-redesContent:hover {
  transform: scale(1.1);
}

@keyframes buttonL {
  0% {
    left: -70px;
  }
  100% {
    left: 150%;
  }
}

@media screen and (max-width: 700px) {
  .header-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 35px;
    gap: 24px;
  }
  .header-content {
    text-align: center;
  }
  .header-content p {
    font-weight: 600;
  }
  .header-img img {
    max-width: 160px;
  }
  .header-redes {
    justify-content: center;
  }
}
